import {Injectable} from '@angular/core';

import {AdaptTranslateService} from '../../common/i18n/index';

import {RxRatingConfigTexts} from './adapt-rx-rating.types';

@Injectable({
  providedIn: 'root'
})
export class AdaptRxRatingConfigService {
  constructor(private _ts: AdaptTranslateService) {
  }

  get texts(): RxRatingConfigTexts {
    const translation = this._ts.getCurrentLanguage();

    return {
      rating: {
        tooltip: [
          translation['adapt.rating.like.l1'],
          translation['adapt.rating.like.l2'],
          translation['adapt.rating.like.l3'],
          translation['adapt.rating.like.l4'],
          translation['adapt.rating.like.l5']
        ]
      },
      binary: {
        btnTextLike: translation['adapt.rating.binary.left'],
        btnTextDislike: translation['adapt.rating.binary.right'],
        btnTextUndo: translation['adapt.common.undo'],
        quotesLeft: translation['adapt.common.doubleQuotesLeft'],
        quotesRight: translation['adapt.common.doubleQuotesRight'],
        likeMessage: translation['adapt.rating.binary.like'],
        dislikeMessage: translation['adapt.rating.binary.dislike'],
        textMessage: translation['adapt.rating.binary.confirm']
      },
      like: {
        tooltipTerrible: translation['adapt.rating.like.l1'],
        tooltipDisliked: translation['adapt.rating.like.l2'],
        tooltipOk: translation['adapt.rating.like.l3'],
        tooltipGood: translation['adapt.rating.like.l4'],
        tooltipExcellent: translation['adapt.rating.like.l5']
      },
      voting: {
        thanksText: translation['adapt.rating.vote.thank'],
        thanksAbbrMessage: translation['adapt.rating.vote.confirm']
      }
    };
  }
}
